    width: 150px;
    margin: 0 10px 0 10px;
}
.userHeaderMenu{
    font-family: 'Neucha';
    font-size: 30px;
    color: #58524C;  
}
.txtWelcome{
    text-align: center;
    font-family: 'Neucha';
    font-size: 45px;
    color: #58524C;
    margin: 55px 0 0 0;
}
.txtIntro{
    text-align: center;
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
    margin: 0 0 30px 0;
}
.cointenerIntro{
