}
.wiatressBlock{
    margin: 20px 100px 0 500px;
}
.waitressImg{
    width: 110px;
    height: auto;
  
}
.chefBlock{
   margin: 0px 0 0 0;
}
.chefImg{
    width: 100px;
    height: auto;
}


.userNameTxt{
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
    margin-bottom: 10px;
}
.cointenerHome{
    text-align: center;
    margin: 0 auto;
}
.logo{
    margin: 30px 0 0 0;
    max-width: 25%;
    text-align: center;
}
.userName{
    font-family: 'Neucha';
    font-size: 25px;
    text-align: center;
    margin-top: 20px;
    width: 30%;
    padding: 20px;
    border-radius: 50px;
    border: solid #EAD4B0;
    background: #F5EAD6;
    color: #58524C;
}
.buttonsHome{
    margin-top: 20px;
    width: 200px;
}
.txtMenu{
    text-align: center;
