    color: #58524C;
}
.buttonsMenu{
    margin-top: 20px;
    width: 200px;
}
.goBreakfastMenu{
    margin: 0 50px 0 500px;
}
.menuBreackfastImg{
    margin-top: 25px;
    width: 200px;
    text-align: center;
}
.goDinnerMenu{
    margin: 10px 0 0 50px;
}
.menuDinnerImg{
    width: 200px;
    text-align: center;
}

.cointenerButtonProduct{
    display: inline-block;
    background: beige;
    width: 200px;
    padding: 20px;
    margin: 5px;
    border-radius: 5%;
    border: solid #EAD4B0;
}
.cointenerButtonsDinner{
    margin: 0;
}
.imgButtonProduct{
    max-height: 80px;
}
.nameButtonProduct{
    font-family: 'Neucha';
    font-size: 25px;
    color: #58524C;
    margin-left: 10px;
}
.buttonPlusProduct{
    width: 50px;
    height: 50px;
    margin: 19px 20px 0 15px;
    background: #56cdb0;
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
    border-radius: 50%;
    border: none;
}
.cointenerComanda{
    font-family: 'Neucha';
    font-size: 45px;
    color: #58524c;
    background: beige;
    width: 800px;
    height: auto;
    text-align: center;
    padding: 50px 20px 20px 20px;
    margin: 150px 50px 0 10px;
    border-radius: 5%;
    border: solid #EAD4B0;
}
.numberTable{
    font-size: 30px;
    margin: 0;
}
.inputNumberTable{
    font-family: 'Neucha';
    font-size: 25px;
    text-align: center;
    margin-top: 5px;
    width: 300px;
    padding: 20px;
    border-radius: 50px;
    border: solid #58524C 1px;
    background: #F5EAD6;
    color: #58524C;
}
.listText{
    font-size: 25px;
    list-style:none;
}
.buttonDelete{
    width: 50px;
    height: 50px;
    margin: 19px 0 0 15px;
    background: #56cdb0;
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
    border-radius: 50%;
    border: none;
}
.total{
    font-size: 35px;
    border-top: #58524C 1px;
    text-align: center;
}
.buttonSendToKitchen{
    margin-top: 20px;
    width: 240px;
}
.txtDinnerMenu{
    text-align: center;
    font-family: 'Neucha';
    font-size: 45px;
    color: #58524C;
    margin: 55px 0 35px 0;
}
.cointenerComandaMenuDinner{
    text-align: center;
    height: auto;
    margin: 0 auto;
}
.buttonAndComanda{
    display: flex;
}
.cointenerKitchenCards{
    text-align: center;
    margin: 50px 20px 30px 20px;
 
}
.cointenerCard{
    display: inline-block;
    text-align: center;
    font-family: 'Neucha';
    font-size: 25px;
    width: 300px;
    background: beige;
    padding: 30px;
    margin: 20px;
    border-radius: 5%;
    border: solid #EAD4B0;
    list-style:none;
}
.tableNumber{
    text-align: center;
    padding: 0;
    margin: 0 0 20px 0;
}
.listCard{

    text-align: center;
    margin: 0;
}
.totalKitchen{
    text-align: center;
    margin: 35px 0 25px 0; 
}
.buttonDone{
    width: 150px;
    margin: 10px;
